/*
 * 获取汉字的简码拼音
 */
export const getJP = (data) => {
  let str = data.toUpperCase();
  let len = str.length;
  let c;
  let result = "";
  let t;
  for(let i=0; i<len; i++){
      c = str.substr(i,1);
      t = pinyinCode[c];
      if (""+t == "undefined") t = "";
      result += t;
  }

  return result.toLowerCase();
}

export default {
  getJP
}





